body {
  background-color: black;
  color: white;
}
.App {
  font-family: Arial, Helvetica, sans-serif;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu,
.quiz,
.endScreen {
  width: 500px;
  height: 500px;
  background-color: lightseagreen;
  color: white;
  border-radius: 7px;
  font-size: 15px;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.menu button,
.quiz button,
.endScreen button {
  width: 100%;
  justify-content: center;
  display: flex;
  margin-bottom: 5px;
  align-items: center;
  border: none;
  width: 300px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.637);
  color: white;
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
}
.menu button:hover,
.quiz button:hover,
.endScreen button:hover {
  opacity: 0.8;
  font-size: 150%;
}
